.main-container {
  display: flex;
  flex-direction: row;
  min-height: calc(100vh - 96px); // 최소 높이만 설정
  background: var(--bg-secondary, #f5f5f5);
  transition: background-color 0.3s ease;
  position: relative;

  .store-main-content {
    flex: 1;
    display: flex;
    flex-direction: column;

    .content-section {
      flex: 1;
      padding: 24px;
      background: var(--bg-primary, #ffffff);
      
      @media (max-width: 768px) {
        padding: 0;
        background: var(--bg-secondary, #f5f5f5);
      }
    }
  }
}

.loading-container,
.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 50vh;
  gap: 1.5rem;
  padding: 2rem;
  text-align: center;

  .loading-spinner {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;

    mat-icon.spin {
      font-size: 48px;
      width: 48px;
      height: 48px;
      animation: spin 1s linear infinite;
      color: var(--accent-primary, #667eea);
    }

    p {
      font-size: 1.1rem;
      color: var(--text-secondary, #666);
      margin: 0;
    }
  }

  .empty-icon {
    font-size: 64px;
    margin-bottom: 1rem;
  }

  h3 {
    font-size: 1.3rem;
    font-weight: 600;
    color: var(--text-primary, #222);
    margin: 0 0 0.5rem 0;
  }

  p {
    font-size: 1.1rem;
    color: var(--text-secondary, #666);
    margin: 0 0 1.5rem 0;
  }

  button {
    mat-icon {
      margin-right: 0.5rem;
    }
  }
}

// Shimmer 로딩 스타일
.shimmer-container {
  .shimmer-product {
    width: 100%;
    
    .shimmer-seller {
      display: flex;
      align-items: center;
      gap: 12px;
      margin-bottom: 24px;
      
      .shimmer-circle {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        background: linear-gradient(
          90deg,
          var(--bg-secondary) 0%,
          var(--bg-tertiary, #f0f0f0) 50%,
          var(--bg-secondary) 100%
        );
        background-size: 200% 100%;
        animation: shimmer 1.5s infinite ease-in-out;
      }
      
      .shimmer-seller-name {
        width: 120px;
      }
    }
    
    .shimmer-line {
      height: 16px;
      background: linear-gradient(
        90deg,
        var(--bg-secondary) 0%,
        var(--bg-tertiary, #f0f0f0) 50%,
        var(--bg-secondary) 100%
      );
      background-size: 200% 100%;
      animation: shimmer 1.5s infinite ease-in-out;
      border-radius: 4px;
      margin-bottom: 12px;
      
      &.shimmer-product-name {
        height: 32px;
        width: 60%;
        margin-bottom: 24px;
      }
      
      &.shimmer-price-line {
        height: 20px;
        width: 40%;
        
        &.large {
          height: 36px;
          width: 50%;
        }
      }
      
      &.shimmer-option {
        height: 48px;
        width: 100%;
        margin-bottom: 16px;
      }
    }
    
    .shimmer-price-section {
      margin-bottom: 32px;
    }
    
    .options-section {
      margin-bottom: 24px;
    }
    
    .action-buttons {
      display: flex;
      gap: 12px;
      
      .shimmer-button {
        flex: 1;
        height: 56px;
        background: linear-gradient(
          90deg,
          var(--bg-secondary) 0%,
          var(--bg-tertiary, #f0f0f0) 50%,
          var(--bg-secondary) 100%
        );
        background-size: 200% 100%;
        animation: shimmer 1.5s infinite ease-in-out;
        border-radius: 8px;
      }
    }
  }
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.product-content {
  max-width: 1200px;  // 데스크톱에서 더 넓게
  margin: 0 auto;
  padding: 0 20px;  // 양쪽 여백
  
  @media (max-width: 768px) {
    max-width: 100%;
    padding: 0;
    margin: 0;
  }
  
  .main-content {
    display: grid;
    grid-template-columns: 1fr; // 수직으로 하나씩 배치
    gap: 2rem;
    margin-bottom: 2rem;

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      gap: 0;
      margin-bottom: 0;
    }

    .image-section {
      background: var(--bg-primary, #ffffff);
      border: 1px solid var(--border-color, #e0e0e0);
      padding: 2rem;
      border-radius: 12px;
      box-shadow: 0 2px 8px var(--shadow, rgba(0, 0, 0, 0.1));
      
      @media (max-width: 768px) {
        border: none;
        border-radius: 0;
        box-shadow: none;
        padding: 0;
        margin-bottom: 0;
      }

      .main-image {
        width: 100%;
        aspect-ratio: 1;
        border-radius: 8px;
        overflow: hidden;
        background: var(--bg-secondary);
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 1rem;
        position: relative;

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }

        .image-fallback {
          position: absolute;
          inset: 0;
          display: flex;
          align-items: center;
          justify-content: center;
          background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);

          .fallback-text {
            font-size: 32px;
            font-weight: 700;
            color: white;
            text-align: center;
            padding: 2rem;
            word-break: break-word;
          }
        }
      }

      .image-thumbnails {
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.5rem 0;
        position: relative;

        .thumbnail-nav-btn {
          flex-shrink: 0;
          width: 32px;
          height: 32px;
          min-width: 32px;
          padding: 0;
          border: 1px solid var(--border-color, #e0e0e0);
          background: var(--bg-primary, #ffffff);
          border-radius: 50%;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
          transition: all 0.2s;
          z-index: 2;

          mat-icon {
            font-size: 20px;
            width: 20px;
            height: 20px;
            color: var(--text-primary, #333333);
          }

          &:hover:not(:disabled) {
            background: var(--bg-secondary, #f5f5f5);
            border-color: var(--accent-primary, #667eea);
            
            mat-icon {
              color: var(--accent-primary, #667eea);
            }
          }

          &:disabled {
            opacity: 0.3;
            cursor: not-allowed;
          }

          &.prev {
            margin-right: 0.25rem;
          }

          &.next {
            margin-left: 0.25rem;
          }
        }

        .thumbnails-container {
          display: flex;
          gap: 0.5rem;
          flex: 1;
          overflow: hidden;
        }

        .thumbnail {
          flex-shrink: 0;
          width: 80px;
          height: 80px;
          border-radius: 4px;
          overflow: hidden;
          cursor: pointer;
          border: 2px solid transparent;
          transition: border-color 0.2s;
          position: relative;
          background: var(--bg-secondary);
          display: flex;
          align-items: center;
          justify-content: center;

          &:hover {
            border-color: var(--border-color);
          }

          &.active {
            border-color: var(--accent-primary, #667eea);
          }

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }

          .thumbnail-fallback {
            position: absolute;
            inset: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);

            span {
              font-size: 20px;
              font-weight: 700;
              color: white;
            }
          }
        }
      }
    }

    .info-section {
      background: var(--bg-primary, #ffffff);
      border: 1px solid var(--border-color, #e0e0e0);
      padding: 2rem;
      border-radius: 12px;
      box-shadow: 0 2px 8px var(--shadow, rgba(0, 0, 0, 0.1));
      display: flex;
      flex-direction: column;
      gap: 1.5rem;
      
      @media (max-width: 768px) {
        border: none;
        border-radius: 0;
        box-shadow: none;
        padding: 16px; // 첫 번째 섹션에만 패딩 유지
        margin-bottom: 0;
      }

      .seller-info {
        display: flex;
        align-items: center;
        gap: 0.5rem;
        padding: 0.75rem;
        background: var(--bg-secondary, #f5f5f5);
        border-radius: 8px;
        cursor: pointer;
        transition: all 0.2s ease;

        &:hover {
          background: var(--hover-color, #e8e8e8);
          transform: translateX(4px);
        }

        .seller-logo-wrapper {
          position: relative;
          width: 32px;
          height: 32px;
          border-radius: 50%;
          overflow: hidden;
          display: flex;
          align-items: center;
          justify-content: center;

          .seller-logo {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }

          .seller-logo-fallback {
            position: absolute;
            inset: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
            
            span {
              font-size: 12px;
              font-weight: 700;
              color: white;
              text-transform: uppercase;
            }
          }
        }

        .seller-name {
          flex: 1;
          font-weight: 500;
          color: var(--text-primary, #222);
        }

        mat-icon {
          color: var(--text-secondary, #666);
        }
      }

      .product-name {
        font-size: 1.8rem;
        font-weight: 700;
        color: var(--text-primary, #222);
        margin: 0;
        line-height: 1.3;
      }

      .rating-section {
          display: flex;
          align-items: center;
          gap: 1rem;

          .stars {
            display: flex;
            align-items: center;
            gap: 0.25rem;

            mat-icon {
              font-size: 20px;
              width: 20px;
              height: 20px;
              color: var(--text-secondary);

              &.filled {
                color: #ffc107;
              }
            }

            .rating-text {
              margin-left: 0.5rem;
              font-weight: 600;
              color: var(--text-primary);
            }
          }

          .review-count {
            color: var(--text-secondary);
          }
        }

        .price-section {
          padding: 1.5rem 0;
          border-top: 1px solid var(--border-color);
          border-bottom: 1px solid var(--border-color);

          .discount-info {
            display: flex;
            align-items: center;
            gap: 0.75rem;
            margin-bottom: 0.5rem;

            .original-price {
              font-size: 1rem;
              color: var(--text-secondary);
              text-decoration: line-through;
            }

            .discount-rate {
              font-size: 1.25rem;
              font-weight: 700;
              color: #e91e63;
            }
          }

          .final-price {
            font-size: 2rem;
            font-weight: 700;
            color: var(--text-primary);
          }
        }

        .options-section {
          display: flex;
          flex-direction: column;
          gap: 1rem;

          .option-row {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .label {
              font-weight: 500;
              color: var(--text-secondary);
            }

            .value {
              font-weight: 600;
              color: var(--text-primary);
            }

            .quantity-selector {
              display: flex;
              align-items: center;
              gap: 0.5rem;
              border: 1px solid var(--border-color);
              border-radius: 4px;
              padding: 0.25rem;

              .quantity {
                min-width: 40px;
                text-align: center;
                font-weight: 600;
              }

              button {
                width: 32px;
                height: 32px;

                mat-icon {
                  font-size: 20px;
                  width: 20px;
                  height: 20px;
                }
              }
            }
          }
        }

        .total-price-section {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 1.5rem;
          background: var(--bg-secondary);
          border-radius: 8px;

          .label {
            font-size: 1.1rem;
            font-weight: 500;
            color: var(--text-secondary);
          }

          .total-price {
            font-size: 1.75rem;
            font-weight: 700;
            color: var(--accent-primary);
          }
        }

        .action-buttons {
          display: grid;
          grid-template-columns: 1fr 2fr;
          gap: 0.75rem;

          button {
            padding: 1rem 2rem;
            font-size: 1.1rem;
            font-weight: 600;
            border-radius: 8px;
            height: auto;

            mat-icon {
              margin-right: 0.5rem;
            }
          }

          .cart-button {
            background: var(--bg-primary);
            border: 2px solid var(--accent-primary);
            color: var(--accent-primary);

            &:hover {
              background: var(--bg-secondary);
            }
          }

          .purchase-button {
            font-size: 1.1rem;
          }
        }
      }
    }

    .tabs-section {
      background: var(--bg-primary);
      border-radius: 8px;
      box-shadow: 0 2px 8px var(--shadow);
      overflow: hidden;
      margin-bottom: 2rem;
      max-width: 1000px;  // 탭 섹션 최대 너비
      margin-left: auto;
      margin-right: auto;
      
      @media (max-width: 768px) {
        border-radius: 0;
        box-shadow: none;
        margin-bottom: 0;
        margin-left: 0;
        margin-right: 0;
        max-width: 100%;
      }

      .tabs-nav {
        display: flex;
        border-bottom: 2px solid var(--border-color);

        .tab-button {
          flex: 1;
          padding: 1.25rem;
          background: none;
          border: none;
          font-size: 1rem;
          font-weight: 500;
          color: var(--text-secondary);
          cursor: pointer;
          transition: all 0.2s;
          position: relative;

          &:hover {
            background: var(--bg-secondary);
            color: var(--text-primary);
          }

          &.active {
            color: var(--accent-primary);
            font-weight: 600;

            &::after {
              content: '';
              position: absolute;
              bottom: -2px;
              left: 0;
              right: 0;
              height: 2px;
              background: var(--accent-primary);
            }
          }
        }
      }

      .tab-content {
        padding: 2rem;
        min-height: 300px;
        max-width: 900px;  // 탭 콘텐츠 최대 너비
        margin: 0 auto;  // 중앙 정렬
        
        @media (max-width: 768px) {
          padding: 0;
          max-width: 100%;
          margin: 0;
        }

        .empty-content {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          gap: 1rem;
          padding: 3rem;
          text-align: center;

          p {
            color: var(--text-secondary);
            font-size: 1.1rem;
          }
        }

        .detail-tab {
          .console-warning-notice {
            display: flex;
            align-items: flex-start;
            gap: 1rem;
            padding: 1rem 1.5rem;
            margin-bottom: 2rem;
            background: linear-gradient(135deg, #fff3cd 0%, #fff8e1 100%);
            border-left: 4px solid #ffc107;
            border-radius: 8px;
            box-shadow: 0 2px 8px rgba(255, 193, 7, 0.2);

            mat-icon {
              color: #f57c00;
              font-size: 24px;
              width: 24px;
              height: 24px;
              flex-shrink: 0;
              margin-top: 2px;
            }

            .notice-content {
              flex: 1;
              font-size: 0.9rem;
              line-height: 1.6;
              color: #5d4037;

              strong {
                display: block;
                margin-bottom: 0.5rem;
                color: #e65100;
                font-weight: 600;
              }

              span {
                display: block;
              }

              code {
                background: rgba(255, 152, 0, 0.15);
                padding: 2px 6px;
                border-radius: 3px;
                font-family: 'Consolas', 'Monaco', monospace;
                color: #d84315;
                font-size: 0.85rem;
              }
            }

            @media (max-width: 768px) {
              display: none;
            }
          }

          .embed-widget-placeholder {
            margin-bottom: 2rem;
            padding: 2rem;
            background: var(--bg-secondary, #f5f5f5);
            border: 2px dashed var(--border-color, #ddd);
            border-radius: 8px;
            text-align: center;
            max-width: 1000px;
            margin-left: auto;
            margin-right: auto;

            .embed-info {
              .embed-url {
                font-size: 0.9rem;
                color: var(--text-secondary, #666);
                word-break: break-all;
                margin-bottom: 0.5rem;
              }

              .embed-status {
                font-size: 1rem;
                font-weight: 600;
                color: var(--primary-color, #667eea);
                margin: 0;
              }
            }
          }

          .embed-widget-container {
            margin-bottom: 2rem;
            position: relative;
            background: var(--bg-secondary);
            border-radius: 8px;
            overflow: hidden !important; // 강제로 모든 스크롤 숨김
            max-width: 1000px;  // embed 컨테이너 최대 너비
            margin-left: auto;
            margin-right: auto;
            height: auto; // 높이 자동 조정
            
            // 스크롤 관련 모든 속성 제거
            -webkit-overflow-scrolling: touch;
            -ms-overflow-style: none;
            scrollbar-width: none;
            
            &::-webkit-scrollbar {
              display: none !important;
            }
            
            &::before {
              content: '콘텐츠 로딩 중...';
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              color: var(--text-secondary);
              font-size: 1rem;
              z-index: 1;
              pointer-events: none;
            }
            
            iframe {
              display: block;
              width: 100%;
              border: none;
              box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
              position: relative;
              z-index: 2;
              background: var(--bg-primary);
              overflow: hidden !important; // 강제로 스크롤바 숨김
              min-height: 300px;
              transition: height 0.1s ease; // 더 빠른 전환
              
              // 모든 스크롤링 관련 속성 제거
              -webkit-overflow-scrolling: touch;
              -ms-overflow-style: none;
              scrollbar-width: none;
              
              &::-webkit-scrollbar {
                display: none !important;
                width: 0 !important;
                height: 0 !important;
              }
            }
          }

          .text-widgets-container {
            display: flex;
            flex-direction: column;
            gap: 1.5rem;
            margin-bottom: 2rem;

            .text-widget-item {
              width: 100%;
              line-height: 1.8;
              color: var(--text-primary);
              font-size: 15px;  // 본문 폰트 크기

              ::ng-deep {
                p, div {
                  margin-bottom: 1rem;
                  font-size: 15px;  // 단락 폰트 크기
                  
                  &:last-child {
                    margin-bottom: 0;
                  }
                }

                strong {
                  font-weight: 600;
                  color: var(--text-primary);
                }

                em {
                  font-style: italic;
                }

                a {
                  color: #1976d2;
                  text-decoration: none;
                  
                  &:hover {
                    text-decoration: underline;
                  }
                }

                ul, ol {
                  margin-left: 1.5rem;
                  margin-bottom: 1rem;
                  font-size: 15px;
                }

                h1 {
                  font-size: 28px;
                  margin-top: 2rem;
                  margin-bottom: 1rem;
                  font-weight: 700;
                  color: var(--text-primary);
                }

                h2 {
                  font-size: 24px;
                  margin-top: 1.75rem;
                  margin-bottom: 0.875rem;
                  font-weight: 600;
                  color: var(--text-primary);
                }

                h3 {
                  font-size: 20px;
                  margin-top: 1.5rem;
                  margin-bottom: 0.75rem;
                  font-weight: 600;
                  color: var(--text-primary);
                }

                h4 {
                  font-size: 18px;
                  margin-top: 1.25rem;
                  margin-bottom: 0.625rem;
                  font-weight: 600;
                  color: var(--text-primary);
                }

                h5, h6 {
                  font-size: 16px;
                  margin-top: 1rem;
                  margin-bottom: 0.5rem;
                  font-weight: 600;
                  color: var(--text-primary);
                }

                hr {
                  border: none;
                  border-top: 1px solid var(--border-color);
                  margin: 1.5rem 0;
                }

                img {
                  max-width: 100%;
                  height: auto;
                  display: block;
                  margin: 1rem auto;
                }
              }
            }
          }

          .image-widgets-container {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            margin-bottom: 2rem;
            max-width: 800px;  // 이미지 컨테이너 최대 너비
            margin-left: auto;
            margin-right: auto;

            .image-widget-item {
              display: flex;
              flex-direction: column;
              align-items: center;
              width: 100%;

              a {
                display: block;
                width: 100%;
                text-decoration: none;
                transition: opacity 0.2s;

                &:hover {
                  opacity: 0.9;
                }
              }

              .widget-image {
                display: block;
                max-width: 100% !important;
                width: 100% !important;
                height: auto !important;
                object-fit: contain;
                margin: 0 auto;
              }

              .detail-image-fallback {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                padding: 3rem 2rem;
                background: var(--bg-secondary, #f5f5f5);
                border: 2px dashed var(--border-color, #ddd);
                border-radius: 8px;
                min-height: 200px;
                gap: 1rem;

                mat-icon {
                  font-size: 48px;
                  width: 48px;
                  height: 48px;
                  color: var(--text-tertiary, #999);
                }

                .image-filename {
                  font-size: 0.95rem;
                  color: var(--text-secondary, #666);
                  text-align: center;
                  word-break: break-all;
                  max-width: 100%;
                  padding: 0 1rem;
                  font-family: 'Courier New', monospace;
                  background: var(--bg-primary, #fff);
                  padding: 0.5rem 1rem;
                  border-radius: 4px;
                  border: 1px solid var(--border-color, #e0e0e0);
                }
              }

              .image-description {
                margin-top: 0.5rem;
                font-size: 0.9rem;
                color: var(--text-secondary);
                text-align: center;
                padding: 0 1rem;
              }
            }
          }

          .json-display {
            background: #f8f9fa;
            border-radius: 8px;
            padding: 2rem;
            margin-bottom: 2rem;

            h3 {
              margin: 0 0 1rem 0;
              font-size: 1.3rem;
              font-weight: 600;
              color: var(--text-primary);
            }

            .json-content {
              background: #282c34;
              color: #abb2bf;
              padding: 1.5rem;
              border-radius: 6px;
              overflow-x: auto;
              font-family: 'Consolas', 'Monaco', 'Courier New', monospace;
              font-size: 0.9rem;
              line-height: 1.6;
              margin: 0;
              box-shadow: inset 0 2px 8px rgba(0, 0, 0, 0.1);
              white-space: pre-wrap;
              word-wrap: break-word;
            }
          }

          .detail-container {
            margin-bottom: 2rem;
            
            .detail-content {
              line-height: 1.8;
              color: var(--text-primary);
              font-size: 1rem;
              width: 100%;
              
              // HTML 콘텐츠 스타일링 (iframe 포함)
              ::ng-deep {
                img {
                  max-width: 100%;
                  height: auto;
                  display: block;
                  margin: 1rem auto;
                }
                
                iframe {
                  max-width: 100%;
                  width: 100%;
                  min-height: 400px;
                  border: none;
                  margin: 1rem 0;
                  display: block;
                  overflow: hidden !important; // 강제로 스크롤바 숨김
                  transition: height 0.1s ease; // 더 빠른 전환
                  
                  // 모든 스크롤링 관련 속성 제거
                  -webkit-overflow-scrolling: touch;
                  -ms-overflow-style: none;
                  scrollbar-width: none;
                  
                  &::-webkit-scrollbar {
                    display: none !important;
                    width: 0 !important;
                    height: 0 !important;
                  }
                  
                  @media (max-width: 768px) {
                    min-height: 300px;
                  }
                }
                
                p {
                  margin-bottom: 1rem;
                }
                
                h1, h2, h3, h4, h5, h6 {
                  margin-top: 1.5rem;
                  margin-bottom: 1rem;
                  font-weight: 600;
                }
                
                ul, ol {
                  margin-left: 2rem;
                  margin-bottom: 1rem;
                }
                
                table {
                  width: 100%;
                  border-collapse: collapse;
                  margin-bottom: 1rem;
                  
                  th, td {
                    border: 1px solid #ddd;
                    padding: 0.75rem;
                    text-align: left;
                  }
                  
                  th {
                    background: var(--bg-secondary);
                    font-weight: 600;
                  }
                }
                
                video {
                  max-width: 100%;
                  height: auto;
                  display: block;
                  margin: 1rem auto;
                }
              }
            }
          }
        }

        .review-tab {
          .review-summary {
            margin-bottom: 2rem;

            h3 {
              font-size: 1.5rem;
              font-weight: 600;
              margin-bottom: 1.5rem;
            }

            .summary-stats {
              .average-rating {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0.75rem;
                padding: 2rem;
                background: var(--bg-secondary);
                border-radius: 8px;

                .rating-number {
                  font-size: 3rem;
                  font-weight: 700;
                  color: var(--text-primary);
                }

                .stars {
                  display: flex;
                  gap: 0.25rem;

                  mat-icon {
                    font-size: 24px;
                    width: 24px;
                    height: 24px;
                    color: var(--text-secondary);

                    &.filled {
                      color: #ffc107;
                    }
                  }
                }

                .total-reviews {
                  color: var(--text-secondary);
                  font-size: 1rem;
                }
              }
            }
          }
        }

        .shipping-tab {
          .shipping-info {
            h3 {
              font-size: 1.5rem;
              font-weight: 600;
              margin-bottom: 1.5rem;
            }

            .info-item {
              padding: 1.5rem;
              border-bottom: 1px solid var(--border-color);

              &:last-child {
                border-bottom: none;
              }

              strong {
                display: block;
                font-size: 1.1rem;
                margin-bottom: 0.5rem;
                color: var(--text-primary);
              }

              p {
                margin: 0;
                color: var(--text-secondary);
                line-height: 1.6;
              }
            }
          }
        }
      }
    }

    .brand-section {
      background: var(--bg-primary);
      padding: 2rem;
      border-radius: 8px;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

      h3 {
        font-size: 1.5rem;
        font-weight: 600;
        margin-bottom: 1.5rem;
      }

      .brand-content {
        display: flex;
        align-items: center;
        gap: 1.5rem;
        padding: 1.5rem;
        background: var(--bg-secondary);
        border-radius: 8px;
        cursor: pointer;
        transition: background-color 0.2s;

        &:hover {
          background: var(--bg-secondary);
        }

        .brand-logo {
          width: 80px;
          height: 80px;
          border-radius: 50%;
          object-fit: cover;
        }

        .brand-info {
          flex: 1;

          h4 {
            font-size: 1.25rem;
            font-weight: 600;
            margin: 0 0 0.5rem 0;
            color: var(--text-primary);
          }

          p {
            margin: 0;
            color: var(--text-secondary);
            line-height: 1.6;
          }
        }

        mat-icon {
          color: var(--text-secondary);
          font-size: 32px;
          width: 32px;
          height: 32px;
        }
      }
    }
  }

// ==========================================
// 모바일 최적화 (768px 이하)
// ==========================================
@media (max-width: 768px) {
  .product-content {
    max-width: 100%;
    padding: 0; // 좌우 패딩 완전 제거
    
    .main-content {
      gap: 0;
      margin-bottom: 0;
    }

    .info-section {
      padding: 12px; // 최소 패딩만

      .seller-info {
        padding: 0.75rem;
        margin-bottom: 0.75rem;

        .seller-logo {
          width: 32px;
          height: 32px;
        }

        .seller-name {
          font-size: 0.9rem;
        }
      }

      .product-name {
        font-size: 1.4rem;
      }

      .rating-section {
        .stars {
          mat-icon {
            font-size: 18px;
            width: 18px;
            height: 18px;
          }

          .rating-text {
            font-size: 0.9rem;
          }
        }

        .review-count {
          font-size: 0.85rem;
        }
      }

      .price-section {
        padding: 1rem 0;

        .discount-info {
          .original-price {
            font-size: 0.9rem;
          }

          .discount-rate {
            font-size: 1.1rem;
          }
        }

        .final-price {
          font-size: 1.6rem;
        }
      }

      .options-section {
        gap: 0.75rem;

        .quantity-selector {
          button {
            width: 32px;
            height: 32px;
            min-width: 32px;

            mat-icon {
              font-size: 20px;
              width: 20px;
              height: 20px;
            }
          }

          input {
            width: 50px;
            font-size: 1rem;
          }
        }
      }

      .total-price-section {
        padding: 1rem;
        margin-top: 1rem;

        .label {
          font-size: 1rem;
        }

        .total-price {
          font-size: 1.5rem;
        }
      }

      .action-buttons {
        gap: 0.5rem;
        margin-top: 1rem;

        button {
          padding: 0.875rem 1rem;
          font-size: 1rem;

          mat-icon {
            font-size: 20px;
            width: 20px;
            height: 20px;
            margin-right: 0.375rem;
          }
        }
      }
    }

    .image-section {
      .main-image {
        height: 300px;

        img {
          object-fit: contain;
        }
      }

      .image-thumbnails {
        padding: 0.5rem;
        gap: 0.5rem;

        .thumbnail-nav-btn {
          width: 28px;
          height: 28px;
          min-width: 28px;

          mat-icon {
            font-size: 18px;
            width: 18px;
            height: 18px;
          }
        }

        .thumbnails-container {
          gap: 0.5rem;
        }

        .thumbnail {
          width: 60px;
          height: 60px;
        }
      }
    }

    // 탭 섹션 모바일 최적화
    .tabs-section {
      margin-top: 1rem;
      border-radius: 0;

      .tab-headers {
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        white-space: nowrap;
        gap: 0;

        &::-webkit-scrollbar {
          display: none;
        }

        .tab-header {
          flex: 0 0 auto;
          min-width: 80px;
          padding: 1rem 0.75rem;
          font-size: 0.9rem;
        }
      }

      .tab-content {
        padding: 0; // 모바일에서 패딩 완전 제거
        max-width: 100%;

        // 본문 내용 100% 너비로 표시
        .detail-tab {
          .embed-widget-container,
          .html-widget-container,
          .text-widgets-container,
          .image-widgets-container {
            max-width: 100%;
            width: 100%;
            margin: 0;
            padding: 0;

            .text-widget-item,
            .html-widget-content {
              padding: 0.5rem;
              font-size: 0.95rem;
              line-height: 1.6;

              h1 {
                font-size: 1.6rem;
                margin-top: 1.5rem;
                margin-bottom: 0.75rem;
              }

              h2 {
                font-size: 1.4rem;
                margin-top: 1.25rem;
                margin-bottom: 0.625rem;
              }

              h3 {
                font-size: 1.2rem;
                margin-top: 1rem;
                margin-bottom: 0.5rem;
              }

              h4, h5, h6 {
                font-size: 1.1rem;
                margin-top: 0.875rem;
                margin-bottom: 0.375rem;
              }

              p {
                margin-bottom: 0.875rem;
              }

              img {
                width: 100%;
                height: auto;
                margin: 0.75rem 0;
              }
            }

            .image-widget-item {
              .widget-image {
                width: 100% !important;
                height: auto !important;
              }

              .detail-image-fallback {
                padding: 2rem 1rem;
                min-height: 150px;

                mat-icon {
                  font-size: 36px;
                  width: 36px;
                  height: 36px;
                }

                .image-filename {
                  font-size: 0.85rem;
                  padding: 0.4rem 0.8rem;
                }
              }

              .image-description {
                font-size: 0.85rem;
                padding: 0 0.5rem;
              }
            }
          }
        }

        // 리뷰 탭
        .review-tab {
          .review-summary {
            padding: 1rem;

            .summary-score {
              .score {
                font-size: 2.5rem;
              }

              .stars {
                mat-icon {
                  font-size: 20px;
                  width: 20px;
                  height: 20px;
                }
              }
            }

            .rating-breakdown {
              .rating-item {
                gap: 0.5rem;

                .rating-label {
                  min-width: 80px;
                }

                .rating-bar {
                  height: 6px;
                }
              }
            }
          }

          .review-list {
            .review-item {
              padding: 1rem;

              .review-header {
                .reviewer-name {
                  font-size: 0.95rem;
                }

                .review-date {
                  font-size: 0.8rem;
                }

                .stars {
                  mat-icon {
                    font-size: 16px;
                    width: 16px;
                    height: 16px;
                  }
                }
              }

              .review-content {
                font-size: 0.9rem;
              }
            }
          }
        }

        // 배송 정보 탭
        .shipping-tab {
          .shipping-info {
            h3 {
              font-size: 1.25rem;
              margin-bottom: 1rem;
            }

            .info-item {
              padding: 1rem;

              strong {
                font-size: 1rem;
              }

              p {
                font-size: 0.9rem;
              }
            }
          }
        }
      }
    }

    // 브랜드 섹션
    .brand-section {
      padding: 1rem;
      border-radius: 0;

      h3 {
        font-size: 1.25rem;
        margin-bottom: 1rem;
      }

      .brand-content {
        padding: 1rem;
        gap: 1rem;

        .brand-logo {
          width: 60px;
          height: 60px;
        }

        .brand-info {
          h4 {
            font-size: 1.1rem;
          }

          p {
            font-size: 0.9rem;
          }
        }

        mat-icon {
          font-size: 24px;
          width: 24px;
          height: 24px;
        }
      }
    }
  }
}

// ==========================================
// 초소형 모바일 최적화 (480px 이하)
// ==========================================
@media (max-width: 480px) {
  .product-content {
    .info-section {
      padding: 16px; // 첫 번째 섹션만 패딩 유지

      .product-name {
        font-size: 1.2rem;
      }

      .price-section {
        .final-price {
          font-size: 1.4rem;
        }
      }

      .action-buttons {
        grid-template-columns: 1fr;

        button {
          width: 100%;
        }
      }
    }

    .tabs-section {
      .tab-headers {
        .tab-header {
          min-width: 70px;
          padding: 0.875rem 0.5rem;
          font-size: 0.85rem;
        }
      }

      .tab-content {
        padding: 0; // 패딩 완전 제거

        .detail-tab {
          .text-widget-item,
          .html-widget-content {
            font-size: 0.9rem;

            h1 {
              font-size: 1.4rem;
            }

            h2 {
              font-size: 1.2rem;
            }

            h3 {
              font-size: 1.1rem;
            }
          }
        }
      }
    }
  }
}
